  outline: none; }
/* button */
.jump-btn {
  position: relative;
  padding: 10px 33px;
  font-size: 13px;
  color: #8f8f8f;
  border: 1px solid #dedede;
  border-radius: 2px;
  background-color: #fff;
  cursor: pointer;
  overflow: hidden;
  outline: none; }
.jump-btn::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: url(data:image/png;base64,) #2d5aa9 center no-repeat;
    top: 0;
    left: -100%;
    opacity: 0;
    -webkit-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out; }
.jump-btn:hover::before {
    left: 0;
    opacity: 1;
    -webkit-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in; }
.more-btn {
  width: 180px;
  height: 50px;
  border: 1px solid #dedede;
  border-radius: 2px;
  background-color: transparent;
  color: #fff;
