.fadein-bottom1 {
  -webkit-animation: fadeInBottom 0.8s ease;
          animation: fadeInBottom 0.8s ease; }
.fadein-bottom2 {
  -webkit-animation: fadeInBottom 1s ease;
          animation: fadeInBottom 1s ease; }
